}

.header {
  background-color: #eee;
  width: 100%;
  height: 120px;
  border-top: 5px solid #8c363f;
}

.header__container {
  display: flex;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 5px 10px;
  height: 120px;
}

.header__title {
  width: 100%;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  color: black;
  margin: auto;
}

.header__title-link {
  color: black;
  text-decoration: none;
}

.span {
  color: #8c363f;
  font-size: 1.2em;
}

@media only screen and (max-width: 480px) {
  .span {
    display: none;
  }
}

.logo__image {
  border-radius: 100px;
}

.logo__left {
  padding-right: 10px;
}

.logo__right {
  padding-left: 10px;
}

.hero {
  width: 100%;
  height: 500px;
  background-color: #eee;
}

@media only screen and (max-width: 1000px) {
  .hero {
    height: 100%;
  }
}

@media only screen and (max-width: 480px) {
