    display: none;
  }
}

.hero__container {
  position: relative;
}

.nav {
  z-index: 999999;
  position: relative;
  margin: 0 auto;
  width: 840px;
  transition: top ease-out 0.6s;
}

.nav__list {
  border-top: 3px solid #8c363f;
  position: absolute;
  margin-top: 35px;
  display: flex;
  justify-content: center;
}

.nav__link {
  background-color: #eee;
  padding: 10px;
  width: 140px;
  height: 60px;
  text-decoration: none;
  color: #000;
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  display: table-cell;
  text-align: right;
  vertical-align: bottom;
  transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225);
}

.nav__link:hover {
  background-color: #eee;
  color: #8c363f;
  height: 110px;
  transition: all 400ms cubic-bezier(1, -0.515, 0.335, 1.225);
}

@media only screen and (max-width: 1000px) {
  .nav__link {
    height: 50px;
    font-size: 16px;
    width: 115px;
    padding: 7px;
  }
}

@media only screen and (max-width: 720px) {
  .nav__link {
    height: 40px;
    padding: 5px;
    font-size: 14px;
    min-width: 100px;
  }
}

.nav__current {
  background-color: #8c363f;
  color: #eee;
  height: 110px;
}

@media only screen and (max-width: 1000px) {
  .nav__current {
    height: 100px;
  }
}

.nav-fixed {
  z-index: 999999;
  position: relative;
}

.nav-fixed__list {
  max-width: 1000px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border-top: 3px solid #8c363f;
  margin: auto;
  transition: all .9s ease;
  display: flex;
  background-color: #eee;
  justify-content: center;
  -webkit-box-shadow: 0px 3px 8px 1px rgba(0, 0, 0, 0.6);
}

@media only screen and (max-width: 720px) {
  .nav-fixed__list {
    flex-wrap: wrap;
  }
}

.nav-fixed__link {
  max-width: 200px;
  width: 16%;
  min-width: 120px;
  padding: 5px;
  text-decoration: none;
  background-color: #eee;
  color: #000;
  font-size: 18px;
  font-weight: 600;
  display: table-cell;
  text-align: center;
  vertical-align: bottom;
  transition: all .4s ease;
}

.nav-fixed__link:hover {
  background-color: #eee;
  color: #8c363f;
}

@media only screen and (max-width: 1000px) {
  .nav-fixed__link {
    font-size: 16px;
    font-weight: 500;
    max-width: auto;
  }
}

@media only screen and (max-width: 720px) {
  .nav-fixed__link {
    font-size: 14px;
    font-weight: 400;
    flex-wrap: wrap;
    width: 50%;
    max-width: auto;
  }
}

@media only screen and (max-width: 580px) {
  .nav-fixed__link {
    flex-wrap: wrap;
    width: 50%;
  }
}

.nav-fixed__current {
  background-color: #8c363f;
  color: #eee;
}

